.contactContent {
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	align-items: center;
	flex-wrap: wrap;
	width: 100%;
}

.contactInfo {
	flex: 1;
	display: flex;
	flex-direction: column;
	align-items: flex-start;
}

.infoItem {
	display: flex;
	flex-direction: row;
}

.adress{
	display: flex;
	flex-direction: row;
}

.adressInfo {
	display: flex;
	flex-direction: column;
}

.adressInfo p {
	margin: 1px;
}

.map {
	flex:2;
}

img {
	margin-right: 10px;
}