.RGcontent {
	width: 100%;
	margin-top: 20px;
}

.catsAndExpl {
	display: flex;
	flex-direction: row;
	justify-content: flex-start;
}

.cats {
	flex: 1;
	display: flex;
	flex-direction: row;
	justify-content: space-around;
	flex-wrap: wrap;
}

.category {
	background: #008edb;
	color: white;
	padding: 5px;
	align-self: center;
	width: 300px;
	height: 80px;
	text-align: center;
	vertical-align: middle;
	font-size: 1.8em;
	margin: 10px;
}

.category:hover {
	background: #00476d;
	cursor: pointer;
}

.expl {
	flex: 1;
}

.detail {
	
}

#true {
	background: rgb(206, 174, 174);
}