.headerBody {
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	align-items: center;
	background: lightslategray;
	margin: 0px;
	position: fixed;
	top: 0px;
	width: 100%;
}

.logo {
	flex: 1;
	font-size: 1.2em;
}

.menuItems {
	flex: 3;
}

.items {
	display: flex;
	flex-direction: row;
}

.item {
	flex: 1;
	list-style: none;
	text-align: center;
	font-size: 1.2em;
}

.item:hover {
	color: white;
	cursor: pointer;
}