.intro {
	display: flex;
	flex-direction: column;
	justify-content: flex-start;
	align-items: center;
	margin-top: 8vh;
	margin-bottom: 5vh;
}

.kantoorTitle {
	font-size: 3em;
	text-align: center;
	align-self: center;
}

.introText {
	font-size: 1.2em;
	color: grey;
	text-align: center;
}

.kantoorContent {
	display: flex;
	flex-direction: column;
}

.participants {
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	flex-wrap: wrap;
}

.participant {
	flex: 3;
	display: flex;
	flex-direction: column;
	min-width: 400px;
	padding: 0 15px 0 15px;
}

.name {
	color: lightblue;
	font-size: 1.8em;
	text-align: center;
}

.job {
	color: gray;
	text-align: center;
	font-size: 1.3em;
}

.partText {
	text-align: justify;
}

.partText p {
	text-align: center;
}

.contactButton {
	display: flex;
	flex-direction: row;
	justify-content: center;
	align-items: center;
	width: 100%;
	margin-top: 6vh;
}

.cButton {
	border-style: none;
	padding: 10px 30px;
	font-size: 1.5em;
	background: darkcyan;
	color: white;
}

.cButton:hover {
	background: cyan;
	cursor: pointer;
}