.App {
  font-family: 'Open Sans', sans-serif;
}

body {
  margin: 0px;
}

.bodyItems {
  margin: 0px 20%;
}
.headerBody {
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: row;
	        flex-direction: row;
	-webkit-justify-content: space-between;
	        justify-content: space-between;
	-webkit-align-items: center;
	        align-items: center;
	background: lightslategray;
	margin: 0px;
	position: fixed;
	top: 0px;
	width: 100%;
}

.logo {
	-webkit-flex: 1 1;
	        flex: 1 1;
	font-size: 1.2em;
}

.menuItems {
	-webkit-flex: 3 1;
